<template>
  <div class="Posts">

		<h1>{{headerString}}</h1>

		<div v-if="error" class="error">
			<h2>{{error}}</h2>
    </div>

		<div v-if='noUser' class='noUser'>
			<h2>Please select a user from the User page.</h2>
		</div>

		<ul v-if="posts"  class="content">
			<li v-for="item in posts" v-bind:key="item.id">
				<Post :post='item' />
			</li>
		</ul>

  </div>
</template>


<script>
	import Post from '@/components/Post.vue';

	export default {
		name: 'Posts',
		components: {
			Post
		},
		data () {
			return {
				posts: null,
				noUser: null,
				error: null,
				headerString: null
			}
		},
		created () {
			if (this.$route.params.userId) {
				this.headerString = 'Posts by ' + this.$route.params.name;
				this.fetchData()
			} else {
				this.headerString = 'Posts';
				this.noUser = true;
			}
		},
		watch: {
			'$route': ['fetchData', 'clearName']
		},
		methods: {
			fetchData () {
				this.error = this.posts = null;

				fetch('https://jsonplaceholder.typicode.com/posts?userId=' + this.$route.params.userId)
					.then(response => response.json())
					.then(json => {
						if (json.length === 0) {
							this.error = 'No posts found for this user.';
						}
						this.posts = json;
					})
					.catch(err => {
						console.log(err);
						this.error = 'An error occurred; user data not retrieved from server.';
					});

			},
			clearName () {
				if (!this.$route.params.userId || !this.$route.params.name) {
					this.headerString = 'Posts';
				}
			}
		}
	};
</script>


<style scoped>
h3 {
	margin: 40px 0 0;
}
ul {
	list-style-type: none;
	padding: 0;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
}
a {
	color: #42b983;
}
</style>
