<template>
  <div class="users">

    <h1>Users</h1>

		<div v-if="error" class="error">
			<h2>An error occurred; user data not retrieved from server.</h2>
    </div>

		<ul v-if="users"  class="content">
			<li v-for="item in users" v-bind:key="item.id">
				<User :user='item' />
			</li>
		</ul>

  </div>
</template>

<script>

import User from '@/components/User.vue'

export default {
	name: 'Users',
	components: {
		User
	},
	props: {},
	data () {
		return {
			users: null,
			error: null
		}
	},
	created () {
		this.fetchData()
	},
	watch: {
		'$route': 'fetchData'
	},
	methods: {
		fetchData () {
			this.error = this.users = null;

			fetch('https://jsonplaceholder.typicode.com/users')
				.then(response => response.json())
				.then(json => {
					this.users = json;
				})
				.catch(err => {
					console.log(err);
					this.error = true;
				});
		}
	}
}
</script>

