import Vue from 'vue'
import VueRouter from 'vue-router'
import Users from '../views/Users'
import Posts from '../views/Posts'

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    component: Users
  },
  {
    path: '/posts/:userId/:name',
    component: Posts
  },
	{
		path: '/posts*',
		component: Posts
	},
];

const router = new VueRouter({
  mode: 'history',
  base: '/vue',
  routes
});

export default router;
