<template>
	<div class='Post'>

		<h2>{{post.title}}</h2>
		<p>{{post.body}}</p>

		<button v-if='!comments' v-on:click='getComments'>Show comments</button>
		<button v-if='comments' v-on:click='hideComments'>Hide comments</button>

		<ul v-if="comments"  class="content">
			<li v-for="item in comments" v-bind:key="item.id" class='comment'>
				<p><em>{{item.email}}</em></p>
				<h3>{{item.name}}</h3>
				<p>{{item.body}}</p>
			</li>
		</ul>

	</div>
</template>

<script>
export default {
	name: 'Post',
	props: {
		post: Object
	},
	data() {
		return {
			comments: null
		}
	},
	methods: {
		getComments() {
			fetch('https://jsonplaceholder.typicode.com/comments?postId=' + this.post.id)
				.then(response => response.json())
				.then(json => {
					if (json.length === 0) {
						this.comments = [{email: '', title: 'No comments available', body: ''}]
					} else {
						this.comments = json;
					}
				})
				.catch(err => {
					console.log(err);
					alert('An error occurred, no comments retrieved.');
				});
		},
		hideComments() {
			this.comments = null;
		}
	}
};
</script>

